.wrapper {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: "ProximaNova", sans-serif;
  font-size: 1.6rem;
  font-weight: 700;
  min-width: 100px;
  padding: 9px 16px;
  border-radius: 4px;
  cursor: pointer;
  background-color: var(--white);
  border: 1px solid transparent;
  user-select: none;
  + .wrapper {
    margin-left: 16px;
  }
  &.disabled {
    pointer-events: none;
    opacity: 0.5;
  }
}

.icon + .title,
.title + .icon {
  margin-left: 8px;
}
.icon {
  display: inline-block;
  width: 20px;
  text-align: center;
}
.rounded {
  border-radius: 52px;
  box-shadow: 0 2px 8px rgb(0 0 0 / 6%);
  border-color: rgba(22, 24, 35, 0.12);

  &:hover {
    border-color: rgba(22, 24, 35, 0.2);
    background-color: rgba(22, 24, 35, 0.03);
  }
}
.primary {
  background-color: var(--primary);
  color: var(--white);
  border-color: var(--primary);

  &:hover {
    border-color: var(--primary);
    background: linear-gradient(0deg, #0000000f, #0000000f), #fe2c55;
  }
}
.outline {
  border-color: var(--primary);
  color: var(--primary);

  &:hover {
    background-color: #fe2c550f;
    border-color: var(--primary);
  }
}
.small {
  min-width: 88px;
  padding: 4px 16px;
}
.large {
  min-width: 140px;
  padding: 14px 16px;
}
.text {
  &:hover {
    text-decoration: underline;
  }
}
